<template>
    <div @click="loopTitle" class="topTitle">
        <div class="backButton" @click="switchPage">
            <
        </div>
        <div class="bookName">
            <div class="nameContent">
                <div class="nameLoop">
                    <span class="showContain">
                        <div ref="move" class="move">
                            <span>{{ bookName }}</span><span>{{ bookName }}</span>
                        </div>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:"topTitle",
        props:{
            bookName:String
        },
        data(){
            return{
                left:0,
                moveFlag:true
            }
        },
        mounted(){
            requestAnimationFrame(this.loopTitle);
        },
        methods:{
            switchPage(){
                this.$emit('switchPage')
            },
            loopTitle(){
                this.$refs.move.style.left = this.left +'%';    
                if(this.moveFlag){
                    this.left--;
                }else{
                    this.left++;
                }     
                if(this.left == -100){
                    this.moveFlag = !this.moveFlag;
                }else if(this.left == 0){
                    this.moveFlag = !this.moveFlag;
                }
                requestAnimationFrame(this.loopTitle)
            }
        }
    }

</script>
<style scoped lang="scss">
    .topTitle{
        height: 0.4rem;
        font-size: .16rem;
        box-shadow: 0 .005rem .005rem #888888;
        position: fixed;
        top: 0;
        width: 100%;
        background: rgb(239,239,239);
        z-index: 3;
        .showContain{
            width: 51%;
            display: inline-block;
            overflow: hidden;
        }
        .backButton{
            position: fixed;
            height: 100%;
            width: .3rem;
            line-height: .4rem;
        }
        .move{
            width: 200%;
            position: relative;
        }
        .nameContent{
            height: .4rem;
            margin: auto;
            line-height: .4rem;
            overflow: hidden;
            .nameLoop{
                text-align: center;
            }
        }


    }

</style>
